
@font-face { font-display: swap; font-style : normal; src: url("assets/fonts/JosefinSansRomanSemiBold.ttf"); font-family : "Josefin Sans"; font-weight : 600;  }
@font-face { font-display: swap; font-style : normal; src: url("assets/fonts/ElMessiriSemiBold.ttf"); font-family : "El Messiri"; font-weight : 600;  }
@font-face { font-display: swap; font-style : normal; src: url("assets/fonts/JosefinSansRomanRegular.ttf"); font-family : "Josefin Sans"; font-weight : 400;  }
@font-face { font-display: swap; font-style : normal; src: url("assets/fonts/JosefinSansRomanBold.ttf"); font-family : "Josefin Sans"; font-weight : 700;  }
@font-face { font-display: swap; font-style : normal; src: url("assets/fonts/JosefinSansRomanMedium.ttf"); font-family : "Josefin Sans"; font-weight : 500;  }

*{
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
}


* ::-webkit-scrollbar {
  display: none;
}

.top-nav {
  display: flex;
  margin: 2vh 1vw 0 0;
  height: 8vh;
  justify-content: right;
  align-items: center;
}

.top-nav > h1 {
  text-align: left;
  font-size: 3.4vw;
  font-weight: 600;
  margin-right: 10vw;
  color: #FF6A6D;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.top-nav > .nav-item {
  display: flex;
  margin-right: 5vw;
  font-size: 1.8vw;
  font-weight: 600;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

.footer-bar {
  display: flex;
  justify-content: space-around;
  padding: 1%;
  height: 30vh;
  background-color: #252525;
}

.footer-bar > .logo > h1 {
  font-size: 3.5vw;
  font-weight: 500;
  color: white;
}

.footer-bar > .logo > .follow > h2 {
  margin-top: 2vh;
  margin-left: .5vw;
  font-size: 1.7vw;
  font-weight: 400;
  color: white;
}

.footer-bar > .logo > .follow > .social-icons {
  display: flex;
  flex-direction: row;
  margin-left: .6vw;
  margin-top: 1vh;
}

.footer-bar > .logo > .follow > .social-icons > a > img {
  width: 2.2vw;
  height: 2.2vw;
  margin-right: 1vw;
}

.quick-link {
  display: flex;
  flex-direction: column;
}

.quick-link > h2 {
  font-size: 2.1vw;
  font-weight: 500;
  color: white;
}

.bottom-nav {
  display: flex;
  flex-direction: column;
  margin-left: .5vh;
}

.bottom-nav > .nav-item {
  color: white;
  font-size: 1.1vw;
  font-weight: 400;
}

.subscribe > h2 {
  font-size: 2vw;
  font-weight: 500;
  color: white;
}

.subscribe > p {
  font-size: 1.2vw;
  font-weight: 400;
  color: white;
}

.subscribe-form{
  display: flex;
  flex-direction: column;
  margin-top: 2vh;
}

.subscribe-form > input {
  width: 20vw;
  height: 5.6vh;
  font-size: 1.5vw;
  padding: 1vh 2vw;
  border: none;
  border-radius: 1vh;
}

.subscribe-form > input:focus {
  outline: none;
}

.subscribe-form > button {
  margin-top: 2vh;
  background-color: #FF2C2C;
  width: 10vw;
  color: white;
  font-size: 1.5vw;
  font-weight: 600;
  padding: 0.5vh 1vw;
  border: none;
  border-radius: 1vh;

}

.home-page > .search-bg{
  display: flex;
  flex-direction: column;
  color: white;
  margin-bottom: 10vh;
  width: auto;
  height: 75vh;
  border-radius: 40vh 0px 40vh 0px;
  background: linear-gradient(90deg, #FF5F6D 0%, #FFC371 100%);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25), 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
}

.search {
  display: flex;
  margin-top: 10vh;
  flex-direction: column;
  align-items: center;
}

.search > h1{
  font-size: 14vh;
  color: white;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-top: 5vh;
  margin-bottom: 4vh;
}

.search > p {
  font-size: 3vh;
  color: #FFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: "El Messiri";
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.search-input {
  position: relative;
  width: 45vw;
}

.search-input > .input-button {
  position: absolute;
  width: 6vh;
  right: 1vw;
  top: 65%;
  transform: translateY(-50%);
  cursor: pointer;
}

.search-input > input {
  width: 100%;
  height: 8vh;
  padding-top: 1.5vh;
  flex-shrink: 0;
  line-height: 8vh;
  margin-top: 3vh;
  font-size: 5vh;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
  border: none;
  padding-left: 1.5vw;
  border-radius: 1.5vh;
  color: #FF5F6D;
}

.search-input > input::placeholder {
  color: #FF5F6D; /* Replace with your desired color */
}

.search-input > input:focus {
  outline: none;
}

.search-input > input:focus::placeholder {
  color: transparent;
}

.grid-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 4vw;
  grid-gap: 5vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
}

.grid-content > div{
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: start;
  background: rgba(232, 231, 231, 0.583);
  border-radius: 2vh;
  padding: 2vh;
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
}

form >label{
  font-size: 1.5vw;
  font-weight: 600;
  margin-bottom: 1vh;
}

form > input{
  width: 35vw;
  height: 7vh;
  font-size: 1.5vw;
  padding: 1vh 1vw;
  margin: 0 2vh 2vh 0;
  border: 2px solid #FF5F6D;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 1vh;
}

form > textarea{
  width: 35vw;
  height: 20vh;
  font-size: 1.5vw;
  padding: 1vh 1vw;
  margin: 0 2vh 2vh 0;
  border: 2px solid #FF5F6D;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 1vh;
}

.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5vh;
}

.quiz-grid, .result, .start-grid {
  display: flex;
  flex-direction: column;
  width: 35vw;
  align-items: left;
  justify-content: start;
  background: rgba(232, 231, 231, 0.583);
  border-radius: 2vh;
  padding: 5vh 2vw 8vh 2vw;
  margin: 3vh 0 5vh 0;
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
}

.quiz-button {
  width: 10vw;
  color: black;
  background-color: white;
  margin-bottom: 2vh;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.quiz-answer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-top: 5vh;
}

.quiz-answer > button {
  width: 20vw;
  height: 5vh;
  margin-top: 1vh;
  border-radius: 1.3vh;
  font-size: 2.5vh;
}

.quiz-button:hover {
  background-color: #FF5F6D;
  color: white;
}

.quiz-button.correct {
  background-color: rgb(20, 185, 20);
  color: white;
}

.quiz-button.incorrect {
  background-color: rgb(168, 168, 168);
  color: white;
}

